html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  font-size: 1.4rem;
  height: 100%;
  background-color: #222222;
}
@media (max-width: 480px) {
  body {
    min-height: 100%;
  }
}
body::-webkit-scrollbar {
  width: 0.9rem;
}
body::-webkit-scrollbar-thumb {
  background-color: #BD9254;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.posicion {
  margin: 11rem auto 2rem auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

li {
  list-style: none;
}

h1, h2, h3 {
  font-family: "Playfair Display", serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p, a, button {
  font-size: 2rem;
  font-family: "Open Sans", sans-serif;
  color: #B2B2B2;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

/*Componenetes*/
.alerta {
  padding: 0.8333333333rem;
  color: #F3F3F3;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 1rem;
}
.alerta.error {
  background-color: #C2544F;
}
.alerta.exito {
  background-color: #329f00;
}

.boton {
  display: block;
  width: 100%;
}

.boton-azul {
  background-color: #0da6f3;
  color: #F3F3F3;
  padding: 0.5rem;
  border: none;
  outline: none;
  width: 8rem;
  transition: 0.3s ease;
}
.boton-azul:hover {
  background-color: #054362;
}

.boton-gris {
  background-color: #076493;
  padding: 1.5rem 4rem;
  color: #B2B2B2;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  margin: 5rem 0;
  width: auto;
}
@media (max-width: 768px) {
  .boton-gris {
    width: 100%;
  }
}
.boton-gris:hover {
  background-color: #054362;
  cursor: pointer;
}

.boton-eliminar {
  color: #F3F3F3;
  background-color: #C2544F;
  cursor: pointer;
  outline: none;
  border: none;
  padding: 0.5rem 1rem;
}

.boton-cerrar {
  background-color: #222222;
  padding: 1.5rem 4rem;
  color: #F3F3F3;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  margin: 5rem 0;
  width: auto;
  border: 1px solid #F3F3F3;
}
@media (max-width: 768px) {
  .boton-cerrar {
    width: 100%;
  }
}
.boton-cerrar:hover {
  background-color: #090909;
  cursor: pointer;
}
.boton-cerrar:hover {
  background-color: #3c3c3c;
}

.boton-web {
  background-color: #BD9254;
  font-size: 2rem;
  padding: 1rem 3rem;
  transition: background-color 0.3s ease;
  color: #fff;
  border: none;
  cursor: pointer;
  margin: 2rem 0 2rem 0;
}
.boton-web:hover {
  background-color: #000000;
}

.btn-deshabilitar, .btn-eliminar, .btn-habilitar {
  background-color: #525252;
  border: none;
  outline: none;
  color: #F3F3F3;
  width: 8rem;
  font-size: 1.5rem;
  padding: 0.5rem;
  cursor: pointer;
  transition: 0.3s ease;
}
.btn-deshabilitar:hover, .btn-eliminar:hover, .btn-habilitar:hover {
  background-color: #060606;
}

.btn-habilitar {
  background-color: #329f00;
}
.btn-habilitar:hover {
  background-color: #226c00;
}

.btn-eliminar {
  background-color: #C2544F;
}
.btn-eliminar:hover {
  background-color: #a53e39;
}

.swal2-popup {
  font-size: 1.6rem !important;
  font-family: Georgia, serif;
  background-color: #2b2929;
  color: #F3F3F3;
}

.tabs {
  display: flex;
  align-items: center;
  justify-content: center;
}
.tabs button {
  background-color: #BD9254;
  cursor: pointer;
  padding: 2rem;
  color: #F3F3F3;
  margin: 0.5rem;
  transition: 0.3s ease;
  border: 1px solid #111010;
  outline: none;
}
.tabs button:hover {
  background-color: #7b5c30;
}
.tabs button.actual {
  background-color: #111010;
}
@media (max-width: 480px) {
  .tabs button {
    margin: 0;
    height: 8rem;
    font-size: 1.5rem;
  }
}

/*Admin*/
.tabs-admin {
  display: flex;
  justify-content: center;
}
.tabs-admin a {
  background-color: #BD9254;
  color: #F3F3F3;
  padding: 2rem;
  transition: background-color 0.3s ease;
  margin: 1rem;
}
.tabs-admin a:hover {
  background-color: #7b5c30;
}

/*layout*/
.navegador {
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid #B2B2B2;
  position: fixed;
  width: 100%;
  transition: top 0.3s ease;
  top: 0;
  z-index: 10;
}
.navegador .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 114rem;
  margin: 0 auto;
  padding: 1rem;
  flex-wrap: wrap;
}

nav a {
  font-size: 1.2rem;
  text-transform: uppercase;
  padding: 1rem 1.5rem;
  font-weight: 400;
  color: #F3F3F3;
  transition: all 0.3s ease;
}
nav a:hover {
  color: #BD9254;
}
nav a:last-child {
  border: 1px solid #F3F3F3;
}
nav a:last-child:hover {
  border: 1px solid #BD9254;
}
@media (max-width: 480px) {
  nav {
    display: none;
  }
}

.oculto {
  top: -9rem;
}

.mobile {
  display: none;
  margin-right: 2rem;
}
@media (max-width: 480px) {
  .mobile {
    display: block;
  }
}
.mobile picture img {
  background-color: #BD9254;
  padding: 0.5rem;
  border-radius: 1rem;
}

.resize {
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  margin-bottom: 1rem;
}

.f-1 {
  background-color: #111010;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8rem 0;
}
.f-1 .f-1-footer {
  margin-top: 3rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 1024px) {
  .f-1 .f-1-footer {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.f-1 .f-1-footer .contact-info {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
}
@media (max-width: 1024px) {
  .f-1 .f-1-footer .contact-info {
    align-items: center;
  }
}
.f-1 .f-1-footer .address-info {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
@media (max-width: 1024px) {
  .f-1 .f-1-footer .address-info {
    align-items: center;
  }
}
.f-1 picture {
  text-align: center;
}
.f-1 p {
  font-size: 1.5rem;
  margin: 0.3rem;
  font-weight: 200;
}
.f-1 .social {
  padding: 2rem;
}
.f-1 .social a {
  display: inline-block;
  border: 1px solid #BD9254;
  padding: 0.5rem;
  margin: 0 2rem;
  transition: 0.3s ease;
}
.f-1 .social a:hover {
  transform: scale(1.1);
  background-color: rgba(0, 0, 0, 0.5);
}
@media (max-width: 480px) {
  .f-1 .social a {
    margin: 0;
  }
}

.f-2 p {
  text-align: center;
  font-size: 1.4rem;
  padding: 1rem;
}

.main-img {
  height: 100vh;
  display: flex;
  align-items: baseline;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  color: #B2B2B2;
}
@media (max-width: 480px) {
  .main-img {
    align-items: center;
    text-align: center;
  }
}
.main-img h2 {
  font-size: 4.2rem;
  text-transform: uppercase;
  line-height: 2rem;
}
@media (max-width: 480px) {
  .main-img h2 {
    line-height: 4rem;
  }
}
.main-img p {
  text-transform: uppercase;
  font-size: 1.3rem;
}
.main-img a {
  margin-top: 5rem;
}
.main-img p {
  margin-bottom: 5rem;
}

.fondo-inicio {
  background-image: linear-gradient(40deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url(/build/img/main.webp);
  background-size: cover;
}

.dorado {
  color: #BD9254;
}

.reservar {
  padding: 1rem 2rem;
  border: 1px solid #F3F3F3;
  color: #F3F3F3;
  transition: all 0.3s ease;
}
.reservar:hover {
  color: #BD9254;
  border: 1px solid #BD9254;
}

/*servicios*/
.servicios {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 7rem 0;
}
.servicios picture {
  text-align: center;
}
.servicios h2 {
  color: #F3F3F3;
  text-transform: uppercase;
  font-size: 3rem;
  font-weight: 200;
  padding-top: 2rem;
}
.servicios p {
  text-align: center;
  font-size: 1.3rem;
}

.servicio {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 4rem;
}
@media (max-width: 768px) {
  .servicio {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 480px) {
  .servicio {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.servicio div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.servicio div:hover h3 {
  color: #BD9254;
}
.servicio div h3 {
  transition: color 0.3s ease;
}
.servicio picture {
  padding: 2rem 0;
}
.servicio picture img {
  padding: 2rem;
  border: 2px solid #BD9254;
}
.servicio h3 {
  font-size: 2rem;
  color: #F3F3F3;
  text-transform: uppercase;
  font-weight: 200;
}
.servicio h3:hover {
  color: #BD9254;
}

.bg-espertos {
  background-color: #111010;
}

.estilistas {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 768px) {
  .estilistas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 480px) {
  .estilistas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.estilistas .estilista {
  display: flex;
  justify-content: center;
  flex-direction: column;
  border: 1px solid #444444;
  transition: border 0.3s ease;
}
.estilistas .estilista picture,
.estilistas .estilista img {
  width: 100%;
  height: 46rem;
  object-fit: cover;
}
.estilistas .estilista picture {
  overflow: hidden;
}
.estilistas .estilista img {
  transition: all 0.3s ease;
}
.estilistas .estilista img:hover {
  transform: scale(1.1);
}
.estilistas .estilista h3 {
  color: #F3F3F3;
  margin: 3rem 0 1rem 0;
  font-weight: 200;
}
.estilistas .estilista p {
  font-size: 1.6rem;
}
.estilistas .estilista:hover {
  border: 1px solid #BD9254;
}

.main-servicio {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 8em;
  margin: 5rem auto;
}
@media (max-width: 480px) {
  .main-servicio {
    padding: 4rem;
  }
}
.main-servicio h2 {
  color: #F3F3F3;
}
.main-servicio a {
  color: #fff;
}
.main-servicio p {
  text-align: center;
}

.fondo-servicio {
  background-image: linear-gradient(40deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url(/build/img/fondo-servicio.webp);
  background-attachment: fixed;
  background-size: cover;
}

.map {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  margin-top: 2rem;
}
.map h2 {
  color: #F3F3F3;
  font-size: 3rem;
  font-weight: 200;
  margin-top: 1rem;
}

iframe {
  margin: 3rem auto;
}

.contenedor-login {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100vh;
}
@media (max-width: 768px) {
  .contenedor-login {
    grid-template-columns: 1fr;
  }
}

.formulario-contenedor {
  background-color: #111010;
}
.formulario-contenedor form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
}
.formulario-contenedor legend {
  font-size: 3rem;
  color: #fff;
}
.formulario-contenedor fieldset {
  border: none;
  width: 80%;
}

.campo {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.campo label {
  color: #F3F3F3;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
}
.campo input {
  padding: 1rem 1.5rem;
  border-radius: 5px;
  outline: none;
  background-color: #111010;
  border: 1px solid #222222;
  color: #888888;
}
.campo input:focus {
  border: 1px solid #888888;
  color: #fff;
}
.campo textarea {
  padding: 1rem 1.5rem;
  border-radius: 5px;
  outline: none;
  background-color: #111010;
  border: 1px solid #222222;
  color: #888888;
  height: 10rem;
  resize: none;
}
.campo textarea:focus {
  border: 1px solid #888888;
  color: #fff;
}
.campo .img-form {
  text-align: center;
  margin-top: 1rem;
}

.bg-login {
  background-image: linear-gradient(40deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url(/build/img/bg-login.webp);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}
.bg-login .div-login {
  width: 50%;
}
.bg-login .div-login h2 {
  color: #fff;
  font-weight: 200;
}

